
"use client";

import { useState, useEffect, useMemo } from 'react';
import { Card, CardContent } from "@/components/ui/card"; // Removed CardHeader, CardTitle
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from "@/components/ui/tooltip";
import { useTranslations } from "@/hooks/useTranslations";
import { cn } from "@/lib/utils";
import type { Project } from '@/types/project';

const DAYS_IN_WEEK = 7;
const WEEKS_TO_SHOW = 52; // Show approximately 1 year

interface DayData {
  date: string; // YYYY-MM-DD
  level: number; // 0 (no activity) to 4 (high activity)
  projectTitles: string[]; // Titles of projects created on this day
  dayOfMonth: number;
  month: number;
  year: number;
}

interface ContributionHeatmapProps {
  projects: Project[];
}

const getFormattedDate = (date: Date): string => {
  const year = date.getFullYear();
  const month = (date.getMonth() + 1).toString().padStart(2, '0');
  const day = date.getDate().toString().padStart(2, '0');
  return `${year}-${month}-${day}`;
};

export default function ContributionHeatmap({ projects }: ContributionHeatmapProps) {
  const t = useTranslations().contributionHeatmap || {
    title: "Coding Activity",
    subtitle: "Project creation milestones over the past year.",
    tooltipPrefix: "Activity on",
    contributions: "projects created", // Changed from "contributions"
    noContributions: "No projects created", // Changed from "No contributions"
    projectsCreatedOn: "Project(s) created on"
  };

  const [heatmapData, setHeatmapData] = useState<DayData[]>([]);

  const projectCreationMap = useMemo(() => {
    const map = new Map<string, string[]>();
    projects.forEach(project => {
      if (project.createdAt) {
        const titles = map.get(project.createdAt) || [];
        titles.push(project.title);
        map.set(project.createdAt, titles);
      }
    });
    return map;
  }, [projects]);

  useEffect(() => {
    const data: DayData[] = [];
    const today = new Date();
    const endDate = new Date(today); // End date is today
    // Adjust start date to be the Sunday of the week WEEKS_TO_SHOW weeks ago
    const startDate = new Date(today);
    startDate.setDate(today.getDate() - (WEEKS_TO_SHOW * DAYS_IN_WEEK) + 1); // Go back roughly 52 weeks
    startDate.setDate(startDate.getDate() - startDate.getDay()); // Set to previous Sunday

    const currentDay = new Date(startDate);

    while (currentDay <= endDate) {
      const dateStr = getFormattedDate(currentDay);
      const projectsOnDay = projectCreationMap.get(dateStr) || [];
      let level = 0;
      if (projectsOnDay.length > 0) level = 3; // Simple level for now, can be more granular

      data.push({
        date: dateStr,
        level: level,
        projectTitles: projectsOnDay,
        dayOfMonth: currentDay.getDate(),
        month: currentDay.getMonth(),
        year: currentDay.getFullYear(),
      });
      currentDay.setDate(currentDay.getDate() + 1);
    }
    // Ensure we have exactly WEEKS_TO_SHOW * DAYS_IN_WEEK days, padding if necessary for full grid display
    const requiredCells = WEEKS_TO_SHOW * DAYS_IN_WEEK;
    if(data.length < requiredCells) {
        const lastDateInData = data.length > 0 ? new Date(data[data.length - 1].date) : new Date(endDate);
        lastDateInData.setDate(lastDateInData.getDate() + 1); // Start padding from day after last real data
        while(data.length < requiredCells){
            const dateStr = getFormattedDate(lastDateInData);
             data.push({
                date: dateStr,
                level: 0,
                projectTitles: [],
                dayOfMonth: lastDateInData.getDate(),
                month: lastDateInData.getMonth(),
                year: lastDateInData.getFullYear(),
            });
            lastDateInData.setDate(lastDateInData.getDate() + 1);
        }
    } else if (data.length > requiredCells) {
        // This scenario might happen if WEEKS_TO_SHOW * DAYS_IN_WEEK doesn't align perfectly with start/end
        // Trim from the beginning to keep recent data
        data.splice(0, data.length - requiredCells);
    }


    setHeatmapData(data);
  }, [projectCreationMap]);

  const getMonthLabels = (data: DayData[]): { label: string, weekIndex: number }[] => {
    if (!data.length) return [];
    const labels: { label: string, weekIndex: number }[] = [];
    let lastMonth = -1;

    for (let i = 0; i < WEEKS_TO_SHOW; i++) {
      const firstDayOfWeekIndex = i * DAYS_IN_WEEK;
      if (firstDayOfWeekIndex < data.length) {
        const firstDayOfWeek = data[firstDayOfWeekIndex];
        if (firstDayOfWeek.month !== lastMonth) {
            // Show label if it's the first month or if more than 2 weeks have passed since last label
          if (labels.length === 0 || i > (labels[labels.length-1].weekIndex + 2) ) {
            labels.push({
              label: new Date(firstDayOfWeek.year, firstDayOfWeek.month).toLocaleDateString(undefined, { month: 'short' }),
              weekIndex: i,
            });
            lastMonth = firstDayOfWeek.month;
          }
        }
      }
    }
    return labels;
  };

  const monthLabels = getMonthLabels(heatmapData);

  const getActivityColor = (level: number): string => {
    switch (level) {
      case 0: return "bg-muted/30 hover:bg-muted/50"; // Keep for no activity
      case 1: return "bg-primary/30 hover:bg-primary/40"; // Light activity
      case 2: return "bg-primary/60 hover:bg-primary/70"; // Medium activity
      case 3: return "bg-primary hover:bg-primary/90";    // High activity (project created)
      case 4: return "bg-primary/100 hover:bg-primary/90 brightness-110"; // Very high, if needed
      default: return "bg-muted/30 hover:bg-muted/50";
    }
  };

  if (!heatmapData.length) {
    return (
      <section id="activity-heatmap" className="py-16 md:py-20">
        <div className="text-center mb-12 md:mb-16">
          <h2 className="text-3xl md:text-4xl font-bold text-primary">{t.title}</h2>
          <p className="text-md text-muted-foreground mt-2 px-2">{t.subtitle}</p>
        </div>
        <Card className="max-w-4xl mx-auto shadow-xl bg-card/80 backdrop-blur-sm p-4 md:p-6">
          <CardContent className="p-0 text-center text-muted-foreground">
            Loading activity data...
          </CardContent>
        </Card>
      </section>
    );
  }

  return (
    <TooltipProvider>
      <section id="activity-heatmap" className="py-16 md:py-20">
        <div className="text-center mb-12 md:mb-16">
          <h2 className="text-3xl md:text-4xl font-bold text-primary">{t.title}</h2>
          <p className="text-md text-muted-foreground mt-2 px-2">{t.subtitle}</p>
        </div>
        
        <Card className="max-w-4xl mx-auto shadow-xl bg-card/80 backdrop-blur-sm p-4 md:p-6">
          <CardContent className="p-0">
            <div className="overflow-x-auto pb-2">
              <div className="flex mb-2 pl-[3.5rem] relative h-6"> {/* Ensure month labels have space */}
                {monthLabels.map((month, index) => (
                  <div
                    key={month.label + month.weekIndex}
                    className="text-xs text-muted-foreground absolute"
                    style={{ left: `calc(${month.weekIndex} * (100% / ${WEEKS_TO_SHOW}))` }}
                  >
                    {month.label}
                  </div>
                ))}
              </div>
              <div className="flex gap-1">
                <div className="grid grid-rows-7 gap-1 mr-2 shrink-0 pt-1"> {/* Added pt-1 for alignment with cells */}
                  {['S', 'M', 'T', 'W', 'T', 'F', 'S'].map((day, index) => (
                     <div key={`${day}-${index}`} className="h-3 w-6 md:h-4 md:w-6 text-xs text-muted-foreground flex items-center justify-center">{index % 2 !== 0 ? day : ''}</div>
                  ))}
                </div>
                <div className="grid grid-flow-col auto-cols-max grid-rows-7 gap-1">
                  {heatmapData.map((day, index) => (
                    <Tooltip key={day.date + index}>
                      <TooltipTrigger asChild>
                        <div
                          className={cn(
                            "h-3 w-3 md:h-4 md:w-4 rounded-sm cursor-default",
                            getActivityColor(day.level)
                          )}
                        />
                      </TooltipTrigger>
                      <TooltipContent>
                        <p className="text-sm font-semibold">
                          {day.projectTitles.length > 0
                            ? `${day.projectTitles.length} ${t.contributions} on ${new Date(day.date + 'T00:00:00').toLocaleDateString(undefined, { year: 'numeric', month: 'long', day: 'numeric' })}`
                            : `${t.noContributions} on ${new Date(day.date + 'T00:00:00').toLocaleDateString(undefined, { year: 'numeric', month: 'long', day: 'numeric' })}`}
                        </p>
                        {day.projectTitles.length > 0 && (
                          <ul className="list-disc list-inside mt-1 text-xs">
                            {day.projectTitles.map(title => <li key={title}>{title}</li>)}
                          </ul>
                        )}
                      </TooltipContent>
                    </Tooltip>
                  ))}
                </div>
              </div>
            </div>
             <div className="flex justify-end items-center mt-4 text-xs text-muted-foreground">
                <span>{t.noContributions}</span>
                <div className="h-3 w-3 md:h-4 md:w-4 rounded-sm bg-muted/30 mx-1"></div>
                <div className="h-3 w-3 md:h-4 md:w-4 rounded-sm bg-primary/30 mx-1"></div>
                <div className="h-3 w-3 md:h-4 md:w-4 rounded-sm bg-primary/60 mx-1"></div>
                <div className="h-3 w-3 md:h-4 md:w-4 rounded-sm bg-primary mx-1"></div>
                <span>{t.contributions.split(' ')[0]}</span>
            </div>
          </CardContent>
        </Card>
      </section>
    </TooltipProvider>
  );
}
